body{max-width:100%;height:100%;margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}nav{padding:25px 0;width:100%}ul li{list-style:none}.todo-item{display:flex;flex-direction:row;align-content:flex-start;margin:10px 25px}.todo-item .todo-item-text{flex-grow:2;border-radius:8px;padding:10px;margin:0px 5px;text-align:start;background-color:#fff;box-shadow:rgba(0,0,0,.19) 0px 10px 20px,rgba(0,0,0,.23) 0px 6px 6px}.todo-item .todo-item-check,.todo-item .todo-item-edit,.todo-item .todo-item-delete{width:50px;border-radius:8px;padding:10px;margin:0px 5px;color:#006fb3;background-color:#fff;box-shadow:rgba(0,0,0,.19) 0px 10px 20px,rgba(0,0,0,.23) 0px 6px 6px}.todo-item input[type=checkbox]{width:18px;height:18px;cursor:pointer}.todo-item input[type=checkbox]:after{content:" ";background-color:#fff;width:18px;height:18px;border:1px solid #006fb3;border-radius:3px;display:inline-block;visibility:visible}.todo-item input[type=checkbox]:checked:after{content:"âœ”";text-align:center;color:#fff;background-color:#006fb3}